import { datadogRum } from "@datadog/browser-rum"
import { clerkOverrides } from "./utils/clerkOverrides"

type HandleClerkLoadProps = {
  isLoaded: boolean
  initialLoginId: string | undefined
  isInitialLogin: boolean | undefined
}

const handleClerkLoad = ({ isLoaded, initialLoginId, isInitialLogin }: HandleClerkLoadProps) => {
  if (isLoaded) {
    if (isInitialLogin) {
      datadogRum.addAction("Create User Custom Action End", { initialLoginId })
    }
    clerkOverrides()
  }
}

export { handleClerkLoad }
